@keyframes plan-flying-plane {
  0% {
    left: 50px;
  }
  10% {
    left: 100px;
  }
  20% {
    left: 150px;
    transform: rotate(0deg);
  }
  25% {
    transform: rotate(10deg);
  }
  40% {
    left: 180px;
    transform: rotate(0deg);
  }
  60% {
    transform: rotate(-20deg);
  }
  70% {
    transform: rotate(10deg);
  }
  100% {
    left: 285px;
    transform: rotate(00deg);
    motion-offset: 100%;
    offset-distance: 100%;
  }
}

@keyframes register-flying-plane {
  
  55%{
    transform: rotateY(180deg) rotateZ(170deg);
    
    right: 246px;
    bottom: 212px;
    offset-distance: 80%;
    offset-position: 80%;
  }
  75%{
    transform: rotateY(180deg) rotateZ(170deg);

    right: 246px;
    bottom: 212px;
    offset-distance: 80%;
    offset-position: 80%;
  }
  
  96% {
    transform: rotateY(180deg) rotateZ(165deg);
    right: 315px;
    bottom: 175px;
    offset-distance: 100%;
    offset-position: 100%;
  }
  100%{
    transform: rotateY(180deg) rotateZ(170deg);
    right: 315px;
    bottom: 175px;
    offset-distance: 99%;
    offset-position: 99%;
  }
}
@keyframes move-plane-one {
  0%{
    left: 166px;
    top: -40px;
  }
  100%{
    left: 112px;
    top: -34px;
    transform: rotate(-5deg) ;
  }
}
@keyframes move-plane-two {
  0%{
    left: 209px;
  }
  100%{
    left: 179px;
    transform: rotate(5deg) ;
  }
}

@keyframes appearing-plan {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
